@media screen and (max-width: 780px) {
  .Process-title {
      font-size: 45px;
  }
  .map .contact-content {
      display: flex;
      flex-direction: column;
  }
  .map .contact-content .visit {
      width: 0;
  }
  .map .contact-content img {
      width: 100%;
  }
  .contact-content .contact-address-item {
      flex: 1;
      background-color: red;
      margin-right: 0;
  }
  .tab-ul>.tab-li {
      flex: 1;
      font-size: 12px;
  }
  .main-content .main-wrap .superiority ul.left,
  .introduction h5,
  .introduction h4,
  .text_about .text-h5,
  .text_about .text-h4 {
      display: none;
  }
  .main-content .main-wrap .superiority .right {
      width: 100%;
      height: 400px;
  }
  .nav-content-3 .jjh .main-title {
      padding: 10px;
  }
  .nav-content-3 .jjh .main-title p.title {
      font-size: 13px;
  }
  .nav-content-3 ul.teams li.team-item p.title {
      font-size: 15px;
  }
  .layui-container {
      width: 100vw;
      padding: 0;
  }
  .layui-tab-content {
      margin-top: 10px;
  }
  .layui-fluid,
  .layui-tab-content,
  .about-info .img-texts .item {
      padding: 0;
  }
  .nav-content-4 .about-main-title {
      padding: 10px;
  }
  .nav-content-4 ul.process li.process-item {
      padding: 5px;
  }
  .nav-content-4 ul.process li.process-item span.title {
      padding: 4px;
      border-radius: 10px;
      -webkit-border-radius: 10px;
      -moz-border-radius: 10px;
      -ms-border-radius: 10px;
      -o-border-radius: 10px;
  }
  .nav-content-4 ul.process li.process-item span.title span.num {
      font-size: 20px;
  }
  .nav-content-4 ul.process li.process-item span.title .title-main {
      flex: 1;
      margin-left: 5px;
  }
  .nav-content-4 ul.process li.process-item span.title .title-main i.icon {
      display: block;
      width: 32px;
      height: 32px;
  }
  .nav-content-4 ul.process li.process-item span.title .title-main p.title-text {
      font-size: 14px;
      line-height: 28px;
  }
  .nav-content-4 ul.process li.process-item span.main-line {
      width: 15px;
      height: 4px;
      border-radius: 2px;
  }
  .nav-content-4 ul.process li.process-item span.text-item {
      font-size: 14px;
      padding: 10px 5px;
      border-radius: 5px;
      -webkit-border-radius: 5px;
      -moz-border-radius: 5px;
      -ms-border-radius: 5px;
      -o-border-radius: 5px;
  }
  .nav-content-4 ul.process li.process-item span.line {
      width: 10px;
      height: 2px;
      border-radius: 2px;
  }
  .nav-content-4 ul.process li.process-item:nth-of-type(2n)::after,
  .nav-content-4 ul.process li.process-item span:nth-child(n+8) {
      display: none;
  }
  .nav-content-4 ul.services li.service-item .service-body p.service-title {
      font-size: 15px;
  }
  .introduction .exploitation ul.types li.type-item p.title {
      font-size: 16px;
      font-weight: 700;
  }
  .nav-content-3 ul.teams li.team-item i.icon {
      width: 100px;
      height: 100px;
      background-size: 100px 100px;
  }
}